import React, {Component} from 'react';
import logo from './logo.svg';
import Nav from './components/Nav.js';
import Header from './components/Header.js';
import ReadContent from './components/ReadContent.js';
import CreateContent from './components/CreateContent.js';
import Control from './components/Control.js';
import Timer from './components/Timer.js';
import './App.css';





class App extends React.Component {
  constructor(props){
    super(props);
    this.max_content_id=3;
    this.state={
      contents:[
        {id:1,title:"ReactÎûÄ?",desc:"ReactÎäî ÌéòÏù¥Ïä§Î∂Å Ïû¨Îã®ÏóêÏÑú ÎßåÎì† SPA(Single Page Application)ÏúºÎ°ú Í∞ÅÏ¢Ö Ïï±(ÌéòÏù¥Ïä§Î∂Å, Ïù∏Ïä§ÌÉÄÍ∑∏Îû® Îì±)Ïóê ÏÇ¨Ïö©Îê©ÎãàÎã§."},
        {id:2,title:"componentÎûÄ?",desc:"Ïª¥Ìè¨ÎÑåÌä∏Îäî ÏâΩÍ≤åÎßêÌï¥ htmlÏùÑ Ïú†ÏßÄÎ≥¥Ïàò Î∞è ÏßÅÍ¥ÄÏÑ±ÏùÑ ÎÜíÏù¥Í∏∞ ÏúÑÌï¥ ÏÇ¨Ïö©ÌïòÎäî Î¶¨Ïï°Ìä∏Ïùò Í∏∞Î≥∏Í∞úÎÖêÏúºÎ°úÏÑú, Í∞Å ÏΩîÎìúÎì§ÏùÑ Ïª¥Ìè¨ÎÑåÌä∏Ìôî ÏãúÏºú Îî∞Î°úÎî∞Î°ú Ï†ÄÏû•Ìï† Ïàò ÏûàÏäµÎãàÎã§!"},
        {id:3,title:"propsÎûÄ?",desc:"propsÎûÄ ÏõπÏùÑ Ïù¥Ïö©ÌïòÎäî ÏÇ¨ÎûåÏù¥ Ï°∞Ï†ï Ìï† Ïàò ÏûàÎäî Í∞íÏúºÎ°ú, Ïù¥Ìï¥Î•º ÎèïÍ∏∞ ÏâΩÍ≤å ÏòàÎ•º Îì§Î©¥ Ìú¥ÎåÄÌè∞Ïùò Î≥ºÎ•®Î≤ÑÌäºÍ∞ôÏùÄ Ï°¥Ïû¨ÏûÖÎãàÎã§."},
        {id:4,title:"stateÎûÄ?",desc:"stateÎäî ÎÇ¥Î∂ÄÏùò Í∞úÎ∞úÏûêÍ∞Ä ÏÑ§Ï†ïÌï¥ ÎÜìÏùÄ Í∞íÏúºÎ°ú, Ïù¥Ìï¥Î•º ÎèïÍ∏∞ ÏâΩÍ≤å ÏòàÎ•º Îì§Î©¥ Ìú¥ÎåÄÌè∞ ÎÇ¥Î∂ÄÏùò ÏãúÏä§ÌÖú Í∞ôÏùÄ Ï°¥Ïû¨ÏûÖÎãàÎã§."}

      ],
      mode:"create",
      subject:{title:'component',desc:"Ïª¥Ìè¨ÎÑåÌä∏Îäî ÏâΩÍ≤åÎßêÌï¥ htmlÏùÑ Ïú†ÏßÄÎ≥¥Ïàò Î∞è ÏßÅÍ¥ÄÏÑ±ÏùÑ ÎÜíÏù¥Í∏∞ ÏúÑÌï¥ ÏÇ¨Ïö©ÌïòÎäî Î¶¨Ïï°Ìä∏Ïùò Í∏∞Î≥∏Í∞úÎÖêÏûÖÎãàÎã§."},
      welcome:{title:'Welcome',desc:'Hello,React~'}
    }
  }
  render(){
    var _title,_desc,_article = null;
    if(this.state.mode == "welcome"){
      _title = this.state.welcome.title;
      _desc = this.state.welcome.desc;
      _article = <ReadContent title={_title} desc={_desc}></ReadContent>
    }
    else if(this.state.mode == "read"){
      for(var i =0; i<this.state.contents.length; i++){
        var data = this.state.contents[i];
        if(data.id=== this.state.selected_content_id){
          _title = data.title;
          _desc = data.desc;
          break;
        }
      }
      _article = <ReadContent title={_title} desc={_desc}></ReadContent>
    }
    else if(this.state.mode == "create"){
      _article = <CreateContent onSubmit={function(_title,_desc){
      this.max_content_id = this.max_content_id+1;

      var _contents = this.state.contents.concat(
        {id:this.max_content_id,title:_title,desc:_desc}
      )
      this.setState({contents:_contents});
      }.bind(this)}></CreateContent>
    }

  return (
    <div className="App">
      <div class="video-wrap">
        <iframe src="https://www.youtube.com/embed/XMb0w3KMw00?list=PLuHgQVnccGMCRv6f8H9K5Xwsdyg4sFSdi" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
      </div>
      <header className="App-header">
        <Header title="ÏïàÎÖïÌïòÏÑ∏Ïöî" subTitle="Î¶¨Ïï°Ìä∏ Í≥µÎ∂ÄÌéòÏù¥ÏßÄÏûÖÎãàÎã§ üíª"
        onChange={function(){
          this.setState({mode:"welcome"});
        }.bind(this)}
        >
        </Header>
        {_article}
      <Control onChangeMode={function(_mode){
        this.setState({mode:_mode});
      }.bind(this)}></Control>
        <Nav data={this.state.contents} 
          onChange={function(id){
            this.setState({mode:'read',selected_content_id:Number(id)});
          }.bind(this)}
          />
          <Timer/>
      </header>
    </div>
  );
  }
}

export default App;
